.icon {
    display: none;
  }
  
  @media screen and (max-width: 600px) {
    .topnav a{display: none;}
    .topnav h2{display: none;}
    .topnav a.icon {
      float: left;
      display: block;
    }
  }
  
  @media screen and (max-width: 600px) {
    .topnav.responsive {position: relative;}
    .topnav.responsive .icon {
      position: relative;
      left: 0;
      top: 0;
    }
    .topnav.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
    .topnav.responsive a {
        float: none;
        display: block;
        text-align: left;
      }
      .topnav.responsive .box{
        box-shadow: rgba(0, 0, 0, 0.15) 1.95px 0.5px 2.6px;
      }
  }
  